import React, { useEffect, useRef } from 'react';
import { Container } from 'react-bootstrap';
import { graphql, Link } from 'gatsby';

import SEOHelmet from 'components/helmet/SEO';
import Layout from 'components/Layout';
import PostHeader from 'components/post/Header';
import PostContent from 'components/post/Content';
import PostToc from 'components/post/Toc';
import NameCard from 'components/card/NameCard';

import { parseTitleForPath } from 'utils/text-parser';

import './index.scss';

export default function Template({
  data,
  pageContext,
}: {
  data: {
    post: {
      frontmatter: {
        tags: { color: string; id: string; name: string }[];
        title: string;
        date: string;
        preview?: { localFile: { childImageSharp: { gatsbyImageData: any } } }[];
        writer?: { name: string }[];
      };
      excerpt: string;
      html: string;
      tableOfContents: string;
      images: { image: { childImageSharp: { gatsbyImageData: any } } }[];
    };
    prevPost: { frontmatter: { title: string } };
    nextPost: { frontmatter: { title: string } };
  };
  pageContext: { prevTitle: string; nextTitle: string };
}) {
  const headerRef = useRef<HTMLDivElement>(null);
  const { frontmatter, html, excerpt, tableOfContents, images } = data.post ?? {
    frontmatter: {},
    html: '',
    excerpt: '',
  };

  const backgroundImage =
    frontmatter.preview?.[0]?.localFile?.childImageSharp.gatsbyImageData.images.fallback.src;

  useEffect(() => {
    if (!headerRef.current) {
      return;
    }

    const gradientStyle = `linear-gradient(45deg, #00396b, #00396bAA)`;
    headerRef.current.style.background = gradientStyle;
    if (backgroundImage) {
      headerRef.current.style.backgroundImage = `url(${backgroundImage})`;
      headerRef.current.style.backgroundSize = 'cover';
      headerRef.current.style.backgroundRepeat = 'no-repeat';
      headerRef.current.style.backgroundPosition = 'center center';
      headerRef.current.style.backgroundColor = '#00396b90';
      headerRef.current.style.backgroundBlendMode = 'multiply';
    }
  }, [backgroundImage, headerRef]);

  let imageIndex = -1;
  const contents = html?.replace(/https:\/\/.*GetObject/g, () => {
    imageIndex += 1;
    return images?.[imageIndex]?.image.childImageSharp.gatsbyImageData.images.fallback.src || '';
  });

  return (
    <Layout>
      <SEOHelmet
        title={`QUES | ${frontmatter?.title}`}
        description={excerpt}
        image={backgroundImage || 'https://avatars.githubusercontent.com/u/76085248?s=200&v=4'}
        url={`https://quesdevteam.github.io/post/${parseTitleForPath(frontmatter.title)}`}
      />

      <article className='article'>
        <header ref={headerRef} className='article-header'>
          <PostHeader frontmatter={frontmatter} />
        </header>

        <Container>
          <div className='article-body'>
            <PostContent html={contents} />
            {tableOfContents && <PostToc toc={tableOfContents} />}
          </div>

          <footer className='article-footer'>
            <NameCard name={frontmatter.writer?.[0].name ?? ''} />
          </footer>

          <aside className='article-aside'>
            <div>
              {pageContext.nextTitle && (
                <Link
                  to={`/post/${parseTitleForPath(pageContext.nextTitle)}`}
                  className='article-aside__relative-post'
                >
                  <span className='material-icons'>west</span>
                  <span className='article-aside__relative-post__title'>
                    {pageContext.nextTitle}
                  </span>
                </Link>
              )}
            </div>
            <div>
              {pageContext.prevTitle && (
                <Link
                  to={`/post/${parseTitleForPath(pageContext.prevTitle)}`}
                  className='article-aside__relative-post right'
                >
                  <span className='article-aside__relative-post__title'>
                    {pageContext.prevTitle}
                  </span>
                  <span className='material-icons'>east</span>
                </Link>
              )}
            </div>
          </aside>
        </Container>
      </article>
    </Layout>
  );
}

export const pageQuery = graphql`
  query ($id: String!) {
    post: markdownRemark(id: { eq: $id }) {
      frontmatter {
        title
        date
        tags {
          color
          id
          name
        }
        preview {
          localFile {
            childImageSharp {
              gatsbyImageData
            }
          }
        }
        writer {
          name
        }
      }
      excerpt
      html
      tableOfContents
      images {
        image {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`;
